<template>
  <div>
    <PersonalHeader></PersonalHeader>
    <div class="centerBg">
      <div class="wrapper clearfix">
        <div class="centerLeft">
          <PersonalMenu class="fl" currentMenu="7"></PersonalMenu>
        </div>
        <div class="centerRight">
          <div class="centerTitle">
            <ul>
              <li class="active">意见反馈</li>
            </ul>
          </div>

          <el-form ref="form" :rules="refs" :model="form" label-width="120px" class="content">
            <el-form-item label="意见反馈" prop="propose">
              <el-input type="textarea" v-model="form.propose" placeholder="请填写您的问题或建议" ></el-input>
            </el-form-item>
            <el-form-item label="手机号码" prop="phone">
              <el-col :span="11">
                <el-input v-model="form.phone" placeholder="请填写您的手机号码"></el-input>
              </el-col>
            </el-form-item>
            <el-form-item label="真实姓名" prop="name">
              <el-col :span="11">
                <el-input v-model="form.name" placeholder="请填写您的真实姓名"></el-input>
              </el-col>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('form')">提交</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api from '@/api/personal-api'
  export default {
    name: "index.vue",
    data() {
      return {
        form: {},
        refs: {
          propose: [
            {required: true, message: '请填写您的建议', trigger: 'blur'}
          ],
          phone: [
            {required: true, message: '请填写您的手机号', trigger: 'blur'},
            {len: 11, message: '手机号码格式不正确', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '请输入您的真实姓名', trigger: 'blur'},
          ]

        },
      }
    },
    methods: {
      submitForm: function () {
        this.$refs['form'].validate(valid => {
          if (valid) {
            api.submitOpinion({...this.form}).then(data => {
              this.$message.success('提交成功')
              this.$refs['form'].resetFields();
            });
          }
        });

      }
    },
    components: {
      PersonalHeader,
      PersonalMenu
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  .centerBg {
    width: 100%;
    height: auto;
    padding: 20px 0 100px;
    background: #F4F5FA;
  }

  .centerLeft {
    float: left;
    width: 230px;
  }

  .centerRight {
    float: right;
    width: 950px;
    min-height: 610px;
    background: #fff;

    .centerTitle {
      padding: 0 33px;
      height: 60px;
      border-bottom: 1px solid #E3E4E5;

      ul {
        li {
          height: 58px;
          line-height: 58px;
          float: left;
          color: #333;
          font-size: 16px;
          border-bottom: 2px solid #fff;
        }

        li.active {
          border-bottom: 2px solid #4F74F4;
        }
      }
    }

    .content {
      padding-top: 35px;
      width: 780px;


    }
  }

</style>



